.App {
  text-align: center;
  background-color: #F9D148;
  height: 100vh;
  overflow: scroll;
}
header{
  color: #E93224;
}
footer{
  margin-top: 40px;
}
footer a{
  text-decoration: none;
  color: #E93224;
}
.app-header{
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 30px;
}
.header-logo{
  height: 40px;
  width: 40px;
  margin-right: 10px;
  background: url('./shell_logo.png');
  background-size: contain;
  background-repeat: no-repeat;
}
.records-content{
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.record-table{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 96vw;
  margin-bottom: 20px;
  overflow: scroll;
}
.record-table-head{
  display: -ms-flexbox;
  display: flex;
  width: 96vw;
  height: 50px;
}
.record-table-head-single{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 16vw;
  background-color: #FBE18D;
  color: #CF422B;
  font-weight: 900;
}
.record-single{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 96vw;
  border-bottom: rgb(231, 188, 182) solid 2px;
  background-color: white;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.record-single-detail{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  width: 16vw;
}



